.portfolio {
    height: 1080px;
    display: flex;
    justify-content: center;
    align-items: center;
    .coverflow__container__1P-xE {
        background-color: transparent;
        overflow: visible;
    }

    .bg {
        position: absolute;
        left: 5%;
        top: 0;
        z-index: -1;
        filter: grayscale(60%);
    }

    .youtube {
        position: fixed;
        height: 100vh;
        width: 100vw;
        background-color: rgba(0, 0, 0, 0.897);
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .closed {
        visibility: hidden;
    }
}