body {
  background-image: linear-gradient(110deg, #47c0ac4b 50%, #2cb5e8) !important;
  
}

.App {
  text-align: center;
  font-family: 'Mali', cursive;
}

.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 30vmin;
  filter: contrast(4);
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.vignette {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 200px rgba(0,0,0,0.9) inset;
}

.App::before {
  content: "";
  background-image: url('./assets/images/bg.png');
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 5rem;
  filter: blur(10px) grayscale(60%);
  top: 5%;
  left: 10%;
  bottom: 10%;
  right: 10%;
  position: absolute;
  z-index: -1;   
}

.nav {
  position: fixed;
  bottom: 0;
  height: 45px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.705);

  display: flex;
  justify-content: center;
  align-items: center;
}

.nav p {
  color: rgb(199, 199, 199);
  padding: 0 1rem;
}
